.Header {
  background-color: #222222;
  height: 50px;
  display: flex;
  justify-content: baseline;
  align-items: center;
  padding-left: 5px;
  position: relative;
}

.Burger {
  background-color: white;
  height: 40px;
  width: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-radius: 5px;
}

.Line {
  height: 5px;
  background-color: black;
  width: 27px;
  border-radius: 5px;
}

.Menu {
  background-color: white;
  width: 150px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease;
  position: absolute;
  z-index: 999999;
  box-shadow: 2px 2px 28px #000;
  padding-bottom: 0px;
  
  ul {
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    
    li {
      display: inline;
    }
  }
}

.Open {
  padding-bottom: 10px;
  max-height: 200px;
